/* eslint-disable max-len */
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import clsx from 'clsx';
import moment from 'moment';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Avatar,
  Box,
  Button,
  Card,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  colors,
  makeStyles,
  useTheme,
  useMediaQuery,
  Typography
} from '@material-ui/core';
import getInitials from 'src/utils/getInitials';
import { assignJob } from 'src/actions/workerActions';

function applyPagination(customers, page, limit) {
  return customers.slice(page * limit, page * limit + limit);
}

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  statusField: {
    flexBasis: 200
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    backgroundColor: colors.red[500],
    color: colors.common.white
  }
}));

function Results({
  className, jobs, customer, ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [open, setOpen] = useState(false);
  const [selJob, setSelJob] = useState(null);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  // Usually query is done on backend with indexing solutions
  const paginatedJobs = applyPagination(jobs, page, limit);

  const handleAssignJob = () => {
    dispatch(assignJob(selJob, customer));
    setOpen(false);
  };

  const handleClose = () => {
    setOpen(false);
  };


  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <PerfectScrollbar>
        <Box>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Name
                </TableCell>
                <TableCell>
                  Customer
                </TableCell>
                <TableCell>
                  Service
                </TableCell>
                <TableCell>
                  Sprint
                </TableCell>
                <TableCell>
                  Created at
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedJobs.map((job) => (
                <TableRow
                  hover
                  key={job._id}
                >
                  <TableCell>
                    <Box
                      display="flex"
                      alignItems="center"
                    >
                      <Avatar className={classes.avatar}>
                        {getInitials(`${job.title}`)}
                      </Avatar>
                      <Box ml={2}>
                        <Typography
                          variant="subtitle2"
                          color="textPrimary"
                          underline="none"
                          onClick={() => {
                            setSelJob(job._id);
                            setOpen(true);
                          }}
                        >
                          {`${job.title}`}
                        </Typography>
                      </Box>
                    </Box>
                  </TableCell>
                  <TableCell>
                    {job.customer ? `${job.customer.first_name} ${job.customer.last_name}` : ''}
                  </TableCell>
                  <TableCell>
                    {job.service ? job.service.title : 'No Assigned'}
                  </TableCell>
                  <TableCell>
                    {job.sprint ? job.sprint.title : 'No Sprint'}
                  </TableCell>
                  <TableCell>
                    {moment(job.created_at).format('DD MMM YYYY')}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={jobs.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Remove</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you going to assign this job to this worker?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleAssignJob} color="primary">
            Yes
          </Button>
          <Button onClick={handleClose} color="primary" autoFocus>
            No
          </Button>
        </DialogActions>
      </Dialog>
    </Card>
  );
}

Results.propTypes = {
  className: PropTypes.string,
  jobs: PropTypes.array,
  customer: PropTypes.string,
};

Results.defaultProps = {
  jobs: []
};

export default Results;
