/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
import React, {
  useCallback,
  useState,
  useEffect
} from 'react';
import {
  Box,
  Container,
  Divider,
  Tab,
  Tabs,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import axios from 'src/utils/axios';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { API_BASE_URL } from 'src/config';
import { WORKER } from 'src/config/endpoint';
import Header from './Header';
import Details from './Details';
import Jobs from './Jobs';
import Logs from './Logs';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function WorkerDetailsView(props) {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [worker, setWorker] = useState(null);
  const [jobs, setJobs] = useState(null);
  const [finishedJob, setFinishedJob] = useState(0);
  const [activeJob, setActiveJob] = useState(0);
  const [currentTab, setCurrentTab] = useState('details');
  const tabs = [
    { value: 'details', label: 'Details' },
    { value: 'jobs', label: 'Jobs' },
    // { value: 'logs', label: 'Logs' }
  ];
  const { workerId } = props.match.params;
  const handleTabsChange = (event, value) => {
    setCurrentTab(value);
  };

  const getCustomer = useCallback(() => {
    axios
      .get(`${API_BASE_URL}/${WORKER.GET}/${workerId}`)
      .then((response) => {
        if (isMountedRef.current) {
          setWorker(response.data.worker);
          setJobs(response.data.jobs);
          setFinishedJob(response.data.finished_job);
          setActiveJob(response.data.active_job);
        }
      });
  }, [isMountedRef]);

  useEffect(() => {
    getCustomer();
  }, [getCustomer]);

  if (!worker) {
    return null;
  }

  return (
    <Page
      className={classes.root}
      title="Worker Details"
    >
      <Container maxWidth={false}>
        <Header worker={worker} />
        <Box mt={3}>
          <Tabs
            onChange={handleTabsChange}
            scrollButtons="auto"
            value={currentTab}
            variant="scrollable"
            textColor="secondary"
            className={classes.tabs}
          >
            {tabs.map((tab) => (
              <Tab
                key={tab.value}
                label={tab.label}
                value={tab.value}
              />
            ))}
          </Tabs>
        </Box>
        <Divider />
        <Box mt={3}>
          {currentTab === 'details'
            && (
            <Details
              customer={worker}
              finishedJob={finishedJob}
              activeJob={activeJob}
            />
            )}
          {currentTab === 'jobs' && <Jobs jobs={jobs} />}
          {currentTab === 'logs' && <Logs />}
        </Box>
      </Container>
    </Page>
  );
}


export default WorkerDetailsView;
