import React from 'react';
import { useHistory } from 'react-router';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { useSnackbar } from 'notistack';
import {
  Box,
  Card,
  CardHeader,
  CardContent,
  Button,
  Divider,
  Typography,
  makeStyles
} from '@material-ui/core';
import NotInterestedIcon from '@material-ui/icons/NotInterested';
import GetAppIcon from '@material-ui/icons/GetApp';
import DeleteIcon from '@material-ui/icons/DeleteOutline';
import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { WORKER } from 'src/config/endpoint';

const useStyles = makeStyles((theme) => ({
  root: {},
  deleteAction: {
    color: theme.palette.common.white,
    backgroundColor: theme.palette.error.main,
    '&:hover': {
      backgroundColor: theme.palette.error.dark
    }
  },
  actionIcon: {
    marginRight: theme.spacing(1)
  }
}));

function OtherActions({ className, customerId, ...rest }) {
  const classes = useStyles();
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();

  const deleteWorker = () => {
    axios.delete(`${API_BASE_URL}/${WORKER.DELETE}/${customerId}`,)
      .then(() => {
        enqueueSnackbar('Worker remove', {
          variant: 'success',
          action: <Button onClick={history.push('/app/workers')}>Customer removed</Button>
        });
      });
  };
  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <CardHeader title="Other actions" />
      <Divider />
      <CardContent>
        <Box
          display="flex"
          flexDirection="column"
          alignItems="flex-start"
        >
          <Button>
            <NotInterestedIcon className={classes.actionIcon} />
            Disable Login
          </Button>
        </Box>
        <Box
          mt={1}
          mb={2}
        >
          <Typography
            variant="body2"
            color="textSecondary"
          >
            Remove this workerâ€™s data if you don`t need him anymore, if not please
            be aware that what has been deleted can never brough back
          </Typography>
        </Box>
        <Button
          className={classes.deleteAction}
          onClick={() => { deleteWorker(); }}
        >
          <DeleteIcon className={classes.actionIcon} />
          Delete Worker
        </Button>
      </CardContent>
    </Card>
  );
}

OtherActions.propTypes = {
  customerId: PropTypes.string,
  className: PropTypes.string
};

export default OtherActions;
